import request from '@/helpers/request.js'

const URLLIST = {
  GET_LIST: '/topics',
  GET_DETAIL: '/topic/:id',
  GET_USERINFO: '/user/:name'
}

export default {
  getIndexBlogs({ page = 1, limit = 40 ,tab = 'all'} = { page: 1, limit: 40 ,tab: 'all'}){
    return request(URLLIST.GET_LIST, 'GET' , { page , limit , tab});
  },
  getDetailBlogs({ id }){
    return request(URLLIST.GET_DETAIL.replace(':id', id));
  },
  getUserInfo({ name }){
    return request(URLLIST.GET_USERINFO.replace(':name', name));
  }
}



