import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/Pages/Index/template.vue'
import Detail from '@/Pages/Detail/template.vue'
import UserInfo from '@/Pages/UserInfo/template.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/detail/:id/:name',
      name: 'detail',
      component: Detail
    },
    {
      path: '/user/:name',
      name: 'user_info',
      component: UserInfo
    }
  ]
})



