export default {
  props: ['blog'],
  name: "PostList",
  computed: {
    tabText (){
      let { good, top, tab } = this.blog;
      return top ? "置顶":
          good ? "精华" :
          tab === "share" ? "分享":
            tab === "ask" ? "问答":
              tab === "job" ? "招聘": "问答";
    },
    isActive (){
      let { good, top} = this.blog;
      return (top || good) ? true : false;
    }
  }
}



