import blog from '@/api/blog.js'
export default {
  data: () => {
    return {
      blog: {},
      id: '',
      loginname: '',
      length: 0,
      userinfo: {},
      name: ''
    }
  },
  created(){
    this.id = this.$route.params.id || '';
    this.name = this.$route.params.name || '';
    if(this.id)
    this.getData(this.id);
    this.getUserInfo(this.name);
  },
  methods: {
    getData(id){
      blog.getDetailBlogs({ id } ).then( res => {
        this.blog = res.data;
        this.loginname = res.data.author.loginname;
        this.length = res.data.replies.length;
      })
    },
    getUserInfo(name){
      blog.getUserInfo({ name } ).then( res => {
        this.userinfo = res.data;
      })
    },
    onfresh(id, name){
      this.id = id;
      this.name = name;
      this.getData(this.id);
      this.getUserInfo(this.name);
      this.$router.push({ name: 'detail', params: {id: id, name: name}});
    }
  },
  computed: {
    tabText (){
      let { good, top, tab } = this.blog;
      return top ? "置顶":
        good ? "精华" :
          tab === "share" ? "分享":
            tab === "ask" ? "问答":
              tab === "job" ? "招聘": "问答";
    },
    isActive (){
      let { good, top} = this.blog;
      return (top || good) ? true : false;
    },
    topcilimitby5(){
      if(this.userinfo.recent_topics){
        return this.userinfo.recent_topics.slice(0,5);
      }
    },
    replylimitby5(){
      if(this.userinfo.recent_replies){
        return this.userinfo.recent_replies.slice(0,5);
      }
    }
  }
}



