<template>
    <header class="header">
      <div class="container">
        <div class="logo">
          <router-link to="/" class="logo-link">
            <img src="@/assets/cnodejs_light.svg" alt="cnode" class="logo-img">
          </router-link>
        </div>
        <div class="nav">
          <nav>
            <ul>
              <li><router-link to="/" style="color: inherit;">首页</router-link></li>
              <li>新手入门</li>
              <li>API</li>
              <li>关于</li>
              <li>注册</li>
              <li>登陆</li>
            </ul>
          </nav>
        </div>
      </div>
    </header>
</template>

<script>
    export default {
        name: "Header"
    }
</script>

<style scoped lang="scss">
  .header{
    background-color: #444444;
    font-size: 13px;
    color: #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    .container{
      display: flex;
      justify-content: space-between;
      width: 90%;
      padding: 5px;
      .logo{
        .logo-link{
          display: inline-block;
          width: 160px;
          height: 34px;
          line-height: 34px;
          padding: 3px 20px;
          font-weight: 700;
          .logo-img{
            height: auto;
            max-width: 100%;
          }
        }
      }

      .nav{
        ul{
          display: flex;
          li{
            padding: 10px 15px;
            line-height: 20px;
            cursor: pointer;
          }
        }
      }
    }
  }

</style>



