import PostList from '@/components/PostList/template.vue'
import blog from '@/api/blog.js'
export default {
  data() {
    return {
      blogs: [],
      page: 1,
      tab: 'all',
      tabText: '',
      isActive: false
    }
  },
  created() {
    this.page = parseInt(this.$route.query.page) || 1;
    this.tab = this.$route.query.tab || 'all';
    this.getDate(this.page, this.tab);
  },
  components: {
    PostList
  },
  methods:{
    onchangePage(newPage){
      this.page = newPage;
      this.getDate(this.page, this.tab);
      this.$router.push({ path: '/', query: {tab: this.tab, page: this.page}})
    },
    onTab(tab){
      this.tab = tab;
      this.getDate(this.page, tab);
      this.$router.push({path: '/', query: {tab: tab}});
    },
    getDate(page, tab){
      blog.getIndexBlogs({ page: page , tab: tab})
        .then(res => {
          this.blogs = res.data;
        })
    }
  }
}



