import blog from '@/api/blog.js'

export default {
  name: "UserInfo",
  data(){
    return {
      userinfo:{},
      name: ''
    }
  },
  methods:{
    getUserInfo(name){
      blog.getUserInfo({ name } ).then( res => {
        this.userinfo = res.data;
      })
    },
  },
  created(){
    this.name = this.$route.params.name || '';
    this.getUserInfo(this.name);//在页面加载之前获取数据
  }
}



